import React, {useContext} from 'react';
import styled from 'styled-components';
import { DataContext } from '../Context/context';

const Container = styled.div`
  position: relative;
  margin: 0;
`

const RatesContainer = styled.div`
  display: flex;
  justify-content: space-around;
  padding: 50px 0;
  z-index: 2;
  position: relative;

  @media (max-width: 1000px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
`

const Title = styled.h2`
padding-left: 20px;
margin: 0;
  transition: all 0.5s ease;
  font-family: 'Bebas Neue', cursive;
  font-size: 50px;
`

const Subtitle = styled.p`
padding-left: 20px;
margin: 0;
font-family: 'Bebas Neue', cursive;
`

const Rates = styled.div`
  padding: 20px;
  flex: 1;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 1px solid white;
  border-radius: 25px;
  margin: 30px 30px;
  background-color: rgb(0,0,0,0.8);
  transition: all 0.5s ease-in-out;

  &:hover {
    border-color: #81E93C;
  }

  &:hover ${Title} {
    color: #81E93C;
  }

  @media (max-width: 1000px) {
    width: 50%;
    align-items: center;
    margin: 20px 20px;
}

@media (max-width: 600px) {
  width: 70%;
  align-items: center;
  margin: 20px 20px;
}

@media (max-width: 400px) {
  width: 90%;
}
`

const Price = styled.h3`
padding-left: 20px;
margin: 5px 0;
`

const Includes = styled.p``

const Ol = styled.ol`
  margin: 0;
  padding: 0;
`

const Li = styled.li`
  margin: 0;
  font-size: 15px;
  padding: 10px;
`

const Br = styled.hr`
height: 1px;
background-color: rgb(200,200,200,0.1);
  border: none;
  margin-left: 10px;
  margin-right: 40px;
`


const SubsPrices = () => {

  const { pricePackage, setPricePackage } = useContext(DataContext);

  return (
    <Container>
        <RatesContainer>
          <Rates onClick={() => { 
            setPricePackage("1");
                }} style={pricePackage === "1" ? {backgroundColor: "#254D32", color: "#81E93C", borderColor:"#81E93C"} : {}}>
                <Title>FULL PACKAGE</Title>
                <Subtitle>3 months program</Subtitle>
                <Price>$90,5</Price>
                <Includes>
                    <Ol>
                        <Li>
                        Lorem ipsum dolor sit
                        </Li>
                        <Br />
                        <Li>
                        Praesent convallis nibh
                        </Li>
                        <Br />
                        <Li>
                        Phasellus auctor augue
                        </Li>
                        <Br />
                    </Ol>
                </Includes>
              </Rates>
              <Rates onClick={() => { 
                setPricePackage("2");
                }} style={pricePackage === "2" ? {backgroundColor: "#254D32", color: "#81E93C", borderColor:"#81E93C"} : {}}>
                <Title>FULL PACKAGE</Title>
                <Subtitle>3 months program</Subtitle>
                <Price>$90,5</Price>
                <Includes>
                    <Ol>
                        <Li>
                        Lorem ipsum dolor sit
                        </Li>
                        <Br />
                        <Li>
                        Praesent convallis nibh
                        </Li>
                        <Br />
                        <Li>
                        Phasellus auctor augue
                        </Li>
                        <Br />
                    </Ol>
                </Includes>
              </Rates>
              <Rates onClick={() => { 
                setPricePackage("3");
                }} style={pricePackage === "3" ? {backgroundColor: "#254D32", color: "#81E93C", borderColor:"#81E93C"} : {}}>
                <Title>FULL PACKAGE</Title>
                <Subtitle>3 months program</Subtitle>
                <Price>$90,5</Price>
                <Includes>
                    <Ol>
                        <Li>
                        Lorem ipsum dolor sit
                        </Li>
                        <Br />
                        <Li>
                        Praesent convallis nibh
                        </Li>
                        <Br />
                        <Li>
                        Phasellus auctor augue
                        </Li>
                        <Br />
                    </Ol>
                </Includes>
              </Rates>
        </RatesContainer> 
    </Container>
  )
}

export default SubsPrices;