import React from 'react'
import styled from 'styled-components'
import logo from "../Imagenes/Logo.png"
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import YouTubeIcon from '@mui/icons-material/YouTube';
import { Link } from "react-router-dom";

const Container = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    padding: 30px 30px 10px 30px;
    color: white;
    position: relative;
    z-index: 5;
`

const ImgCover = styled.div`
    top: 0;
    box-sizing: border-box;
    background-color: #254D32;
    opacity: 0.9; 
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: -1;
`

const CopyRight = styled.span`
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 0 20px 0;
`

const Socials = styled.div`
    display: flex; 
    flex: 1;
    justify-content: end;
    align-items: end;
`

const Logo = styled.img`
    box-sizing: border-box;
    padding: 0 0 5px 0;
    z-index: 2;
    opacity: 0.8;
`

const Icon = styled.div`
    margin: 10px;
    cursor: pointer;
    transition: all 0.5s ease;

    &:hover {
        transform: scale(1.1);
        color: #81E93C;
    }
`

const MenuList = styled.div`
    display: flex;
    justify-content: space-around; 
    align-items: center;
    z-index: 2;
`
const LinkContainer = styled.div`
    display: flex;
    flex-direction: column;
    text-align: center;
`


const Links = styled.a`
    list-style: none;
    cursor: pointer;
    color: white;
    font-size: 15px;
    margin: 0 5px;
    transition: all 0.5s ease;
    text-decoration: none;

    &:hover{
        color: #81E93C;
    }
`

const Button = styled.button`
    padding: 13px 25px;
    margin: 10px 0 15px 0;
    border-radius: 5px;
    border: solid 1px #81E93C;
    background-color: transparent;
    color: white;
    cursor: pointer;
    transition: all 0.5s ease;

    &:hover {
        color: black;
        background-color: #81E93C;
        border-color: black;
    }
`

const FooterContactForm = () => {

  return (
    <Container>
        <Logo src={logo}/>
              <LinkContainer>
                <Link to="/subscription">
                    <Button><b>START NOW</b></Button>
                </Link>
              </LinkContainer>
        <MenuList>
              <LinkContainer>
                <Links href="#Classes">Classes</Links>
              </LinkContainer>
                <p>-</p>        
              <LinkContainer>
                <Links href="#Rates">Rates</Links>
              </LinkContainer>
                <p>-</p>
              <LinkContainer>
                <Links href="#Schedule">Schedule</Links>
              </LinkContainer>
        </MenuList>
        <Socials>
            <Icon>
                <FacebookIcon />
            </Icon>
            <Icon>
                <InstagramIcon />
            </Icon>
            <Icon>
                <TwitterIcon />
            </Icon>
            <Icon>
                <YouTubeIcon />
            </Icon>
        </Socials>
        <CopyRight>CopyRight@</CopyRight>
        <ImgCover />
    </Container>
  )
}

export default FooterContactForm;