import React from 'react';
import styled from 'styled-components';
import AccessTimeIcon from '@mui/icons-material/AccessTime';


const Container = styled.div`
    color: white;
    display: flex;
    padding: 50px;

    @media (max-width: 700px) {
        flex-direction: column;
    }

    @media (max-width: 400px) {
        padding: 50px 30px;
    }
`

const ContainerLeft = styled.div`
padding: 10px 10px 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    border: solid 1px black;
    border-radius: 10px;
    background-color: rgb(0,0,0,0.5 );
`

const ContainerLeft1 = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    width: 40%;
`

const ContainerLeft2 = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: end;
    width: 40%;
`

const Day = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`

const P = styled.p`
padding-top: 1px;
    padding-left: 5px;
    font-family: 'Bebas Neue', cursive;
    weight: bold;
    color: white;
`

const ContainerRight = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex: 1;
    margin-top: -30px;
    padding: 0;

    @media (max-width: 700px) {
        margin-top: 20px;
    }
`

const ClassSchedule = styled.div`
    background-color: rgba(0,0,0,0.5);
    border-radius: 5px;
    border: 1px solid #254D32;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 10px;
    padding: 10px 20px;
`

const ClassTitle = styled.h2`
    font-family: 'Bebas Neue', cursive;
    font-size: 40px;
    margin: 0;
    padding: 10px 0;
    color: #81E93C;
`

const ClassTime = styled.p`
    margin: 0;
    padding: 5px 0;
`

const Hr = styled.hr`
height: 0.5px;
width: 100%;
background-color: rgb(200,200,200, 0.1);
  border: none;
`



const Schedule = () => {
  return (
    <Container id="Schedule">
        <ContainerLeft>
            <ContainerLeft1>
            <Day>
                <AccessTimeIcon style={{ color: "#81E93C"}}/>
                <P> Monday</P>
            </Day>
            <Hr />
            <Day>
                <AccessTimeIcon style={{ color: "#81E93C"}}/>
                <P> Tuesday</P>
            </Day>
            <Hr />
            <Day>
                <AccessTimeIcon style={{ color: "#81E93C"}}/>
                <P> Wednesday</P>
            </Day>
            <Hr />
            <Day>
                <AccessTimeIcon style={{ color: "#81E93C"}}/>
                <P> Thursday</P>
            </Day>
            <Hr />
            <Day>
                <AccessTimeIcon style={{ color: "#81E93C"}}/>
                <P> Friday</P>
            </Day>
            <Hr />
            <Day>
                <AccessTimeIcon style={{ color: "#81E93C"}}/>
                <P> Saturday</P>
            </Day>
            <Hr />
            <Day>
                <AccessTimeIcon style={{ color: "#81E93C"}}/>
                <P> Sunday</P>
            </Day>
            <Hr />
            </ContainerLeft1>
            <ContainerLeft2>
                <P> 7am - 23pm</P>
                <Hr />
                <P> 7am - 23pm</P>
                <Hr />
                <P> 7am - 23pm</P>
                <Hr />
                <P> 7am - 23pm</P>
                <Hr />
                <P> 7am - 23pm</P>
                <Hr />
                <P> 8am - 21pm</P>
                <Hr />
                <P> 10am - 20pm</P>
                <Hr />
            </ContainerLeft2>
        </ContainerLeft>
        <ContainerRight>
            <ClassSchedule>
                <ClassTitle>
                        Crossfit
                </ClassTitle>
                <ClassTime>
                <b>Mon - Vie :</b> 2pm to 20pm
                </ClassTime>
                <ClassTime>
                <b>Sat - Sun :</b> 2pm to 20pm
                </ClassTime>
            </ClassSchedule>
            <ClassSchedule>
                <ClassTitle>
                        Aerobics
                </ClassTitle>
                <ClassTime>
                <b>Mon - Vie :</b> 2pm to 20pm
                </ClassTime>
                <ClassTime>
                <b>Sat - Sun :</b> 2pm to 20pm
                </ClassTime>
            </ClassSchedule>
            <ClassSchedule>
                <ClassTitle>
                        Pilates
                </ClassTitle>
                <ClassTime>
                <b>Mon - Vie :</b> 2pm to 20pm
                </ClassTime>
                <ClassTime>
                <b>Sat - Sun :</b> 2pm to 20pm
                </ClassTime>
            </ClassSchedule>
        </ContainerRight>
    </Container>
  )
}

export default Schedule;