import React from 'react';
import styled from 'styled-components';
import SubsFooter from '../components/SubsFooter';
import Form from '../components/Form';
import SubsNavbar from "../components/SubsNavbar"
import SubsPrices from "../components/SubsPrice"

const Container = styled.div`
    position: relative;
`

const ImgCover = styled.div`
    background-color: black;
    opacity: 0.5;
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: -1;
`

const Subscription = () => {
  return (
    <Container>
        <ImgCover />
        <SubsNavbar />
        <SubsPrices />
        <Form />
        <SubsFooter />
    </Container>
  )
}

export default Subscription;