import React, { useContext } from 'react';
import styled from 'styled-components';
import LogoImg from "../Imagenes/Logo.png";
import { Link } from "react-router-dom";
import { DataContext } from '../Context/context';


const Container = styled.div`
    color: white;
    display: flex;
    justify-content: space-between;
    height: 70px;
    position: relative;
    z-index: 5;

    @media (max-width: 700px) {
        flex-direction: column;
    }
`

const Logo = styled.div`
    box-sizing: border-box;
    padding: 10px 0 5px 30px;
    z-index: 2;
    opacity: 1;
    flex: 2;

    @media (max-width: 400px) {
    transform: scale(0.9);
    padding-left: 0;
    }
`

const Img = styled.img`
`

const Line = styled.div`
    height: 0.5px;
    transition: width 0.3s ease;
    width: 0;
    background-color: #81E93C;
`

const A = styled.a`
    list-style: none;
    cursor: pointer;
    padding: 0 0 5px 0;
    color: white;
    font-size: 17px;
    text-decoration: none;
    transition: all 0.5s ease-in-out;
`

const MenuList = styled.div`
    overflow-x: hidden;
    top: 80px;
    padding: 0 20px 0 50px;
    display: flex;
    justify-content: space-around; 
    align-items: center;
    flex: 1;
    z-index: 2;

    @media (max-width: 1100px) {
        flex: 2;
    }

    @media (max-width: 800px) {
        flex: 3;
    }

    @media (max-width: 700px) {
        position: absolute;
        flex-direction: column;
        align-items: center;
        align-self: end;
    }
`
const LinkContainer = styled.div`
    transition: transform 0.5s ease-in ${props => `${(props.i + 0.5) / 10}s`};

    &:hover ${Line} {
        width: 100%;
    }
    
    @media (max-width: 700px) {
        padding: 5px;
        transform: translateX(${props => props.clicked ? "0px" : "200px"});
    }
`

const Button = styled.button`
    box-sizing: border-box;
    padding: 10px 20px;
    border-radius: 5px;
    border: solid 1px #81E93C;
    background-color: transparent;
    color: white;
    cursor: pointer;
    transition: all 0.5s ease;
    z-index: 5;

    &:hover {
        color: black;
        background-color: #81E93C;
        border-color: black;
    }

    @media (max-width: 700px) {
        display: none;
    }
`

const Hammbmenu = styled.a`
    margin: 0;  
    padding: 0;
    position: absolute;
    top: 40px;
    right: 20px;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 21px;
    cursor: pointer;
    z-index: 5;

    @media (max-width: 700px) {
        display: flex;
    }
` 

const HambMenuLine = styled.div`
    margin: 0;  
    padding: 0;
    height: 3px;
    width: 100%;
    background-color: white;
    border-radius: 5px;
`


const Navbar = () => {

    const Items = ["Classes", "Rates", "Schedule"];

    const { clicked, setClicked } =useContext(DataContext);

    const handleClick = () => {
        setClicked(!clicked);
    }

  return (
    <Container>
        <Logo>
        <Link to="/">
            <Img src={LogoImg}/>
        </Link>
        </Logo>
        <MenuList >
            {Items.map(( item, i )=>  (
              <LinkContainer clicked={clicked} i={i}>
                <A href={`#${item}`} key={item}>{item}</A>
                <Line></Line>
              </LinkContainer>)
              )
            }
              <Link to="/subscription" style={{ color: "inherit" }}>
                <Button><b>START NOW</b></Button>
                </Link>  
        </MenuList>
        <Hammbmenu onClick={handleClick}>
            <HambMenuLine></HambMenuLine>
            <HambMenuLine></HambMenuLine>
            <HambMenuLine></HambMenuLine>
        </Hammbmenu>
    </Container>
  )
}

export default Navbar;