import React from 'react';
import styled from 'styled-components';
import 'animate.css';


const Container = styled.div``

const Span = styled.span`
    min-width: 20px; 
    opacity: 0;
    display: inline-block;
    animation: fadeIn; 
    animation-duration: 0.5s; 
    animation-fill-mode: forwards;
    margin: 0;
    color: white;
    font-size: 100px;
    font-family: 'Bebas Neue', cursive;
    font-weight: 1000;
    animation-delay: ${props => `${props.i / 10}s`};


    @media (max-width: 800px) {
        font-size: 85px;
    }
    
    @media (max-width: 700px) {
        font-size: 75px;
    }

    @media (max-width: 400px) {
        font-size: 65px;
    }

    @media (max-width: 350px) {
        font-size: 50px;
    }
`

const Span1 = styled.span`
    min-width: 20px; 
    opacity: 0;
    display: inline-block;
    animation: fadeIn; 
    animation-duration: 0.5s; 
    animation-fill-mode: forwards;
    margin: 0;
    color: white;
    font-size: 100px;
    font-family: 'Bebas Neue', cursive;
    font-weight: 1000;
    animation-delay: ${props => `${props.i / 10}s`};


    @media (max-width: 800px) {
        font-size: 85px;
    }
    
    @media (max-width: 700px) {
        font-size: 75px;
    }

    @media (max-width: 600px) {
        display: none;
    }

    @media (max-width: 350px) {
        font-size: 50px;
    }
`

const Span2 = styled.span`
    min-width: 20px; 
    opacity: 0;
    animation: fadeIn; 
    animation-duration: 0.5s; 
    animation-fill-mode: forwards;
    margin: 0;
    color: white;
    font-size: 75px;
    font-family: 'Bebas Neue', cursive;
    font-weight: 1000;
    animation-delay: ${props => `${props.i / 10}s`};
    display: none;


    @media (max-width: 600px) {
        display: inline-block;
    }

    @media (max-width: 400px) {
        font-size: 65px;
    }

    @media (max-width: 350px) {
        font-size: 50px;
    }

`

const Br = styled.br`
    display: none;


    @media (max-width: 600px) {
        display: inline-block;
    }
`

const ColoredSpan = styled.span`
    min-width: 20px; 
    opacity: 0;
    display: inline-block;
    animation: fadeIn;
    animation-duration: 0.5s; 
    animation-fill-mode: forwards;
    margin: 0;
    color: #81E93C;
    font-size: 100px;
    font-family: 'Bebas Neue', cursive;
    font-weight: 1000;
    animation-delay: ${props => `${props.i / 10}s`}
    ;

    
    @media (max-width: 800px) {
        font-size: 85px;
    }
    
    @media (max-width: 700px) {
        font-size: 75px;
    }

    @media (max-width: 400px) {
        font-size: 65px;
    }
    
    @media (max-width: 350px) {
        font-size: 50px;
    }
`

const AnimatedLetters = () => {

    const strArray1 = ["b", "e", " "];

    const strArray2 = ["s", "t", "r", "o", "n", "g", "e", "r"]

    const strArray3 = ["t", "h", "a", "n", " ", "y", "o", "u", "r", " ", "e", "x", "c", "u", "s", "e", "s"]

    const strArray4 = ["t", "h", "a", "n", " ", "y", "o", "u", "r"]

    const strArray5 = ["e", "x", "c", "u", "s", "e", "s"]
  
    return (
    <Container>
        {
            strArray1.map(( char, i ) => (
                <Span i={i} key={char + i}>
                    {char}
                </Span>
            ))
        }
        {
            strArray2.map(( char, i ) => (
                <ColoredSpan i={2 + i} key={char + i}>
                    {char}
                </ColoredSpan>
            ))
        }
        <br />
        { 
            strArray3.map(( char, i ) => (
                <Span1 i={10+i} key={char + i}>
                    {char}
                </Span1>))}

        {
            strArray4.map(( char, i ) => (
                <>
                <Span2 i={10+i} key={char + i}>
                    {char}
                </Span2>
                    </>
                ))}
                <br />
        
        { 
            strArray5.map(( char, i ) => (
                <Span2 i={18+i} key={char + i}>
                    {char}
                </Span2>))
        }
    </Container>
  )
}

export default AnimatedLetters;