import React from 'react'
import styled from 'styled-components';
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import YouTubeIcon from '@mui/icons-material/YouTube';
import { Link } from 'react-router-dom';
import AnimatedLetters from "./AnimatedLetters";

const Container = styled.div`
    height: 100vh;
    width: auto;
    position: relative;
    margin: 0;
    padding: 0;

    @media (max-width: 800px) {
        height: 90vh;
    }
`

const InfoContainer = styled.div`
    position: relative;
    margin: 0;
    top: 10%;
    left: 10%;
    z-index: 2;
    width: 90%;
    display: flex;
    flex-direction: column;

    @media (max-width: 800px) {
        height: 90vh;
        padding-top: 50px;
        left: 5%;
    }
`

const TitleContainer = styled.div`
    display: flex;
    flex-direction: column;
    width: 90%;
`

const Desc = styled.span`
    color: white;
    width: 70%;

    @media (max-width: 600px) {
        font-size: 14px;
    }
`

const Button = styled.button`
    margin-top: 10px;
    padding: 10px 20px;
    border-radius: 5px;
    border: solid 1px #81E93C;
    background-color: transparent;
    color: white;
    cursor: pointer;
    transition: all 0.5s ease;

    &:hover {
        color: black;
        background-color: #81E93C;
        border-color: black;
    }
`

const Socials = styled.div`
    position: absolute;
    top: 0;
    right: 20px;
    display: flex; 
    z-index: 12;
    color: white;

    @media (max-width: 700px) {
        right: inherit;
        left: 20px;
    }
`

const Icon = styled.div`
    margin: 10px;
    cursor: pointer;
    transition: all 0.5s ease;

    &:hover {
        color: #81E93C;
        transform: scale(1.1)
    }
`


const Hero = () => {
  return (
    <Container>
        <Socials>
            <Icon>
                <FacebookIcon />
            </Icon>
            <Icon>
                <InstagramIcon />
            </Icon>
            <Icon>
                <TwitterIcon />
            </Icon>
            <Icon>
                <YouTubeIcon />
            </Icon>
        </Socials>
        <InfoContainer>
            <TitleContainer>
                <AnimatedLetters />
            </TitleContainer>
            <Desc>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Adipisci natus voluptatem ipsam rem, hic, consectetur at, ex repudiandae voluptates impedit ad itaque. Voluptates, facere iure. Rem vitae accusamus voluptatibus repellendus eius, mollitia accusantium molestias hic autem, quidem fugiat, obcaecati aut.</Desc>
            <Link to="/subscription" style={{ color: "inherit", width: "fit-content" }}>
                <Button><b>START NOW</b></Button>
                </Link>  
        </InfoContainer>
    </Container>
  )
}

export default Hero;