import { useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import Silhouette from "../Imagenes/Lakeyboy_Silhouette.png"

const Button = styled.button`
  width: 5%;
  color: #254D32;
  background-color: transparent;
  z-index: 10;
  border: none;
  cursor: pointer;
  z-index: 10;
  font-size: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: font-size 0.2s ease-in-out;
  position: absolute;
  top: 0;
  bottom: 0;
  left: ${props => props.direction === "left" ? "0" : "95%"};
  right: ${props => props.direction === "right" ? "0" : "95%"};
  margin: auto;
  

  &:hover {
    font-size: 4.5rem; 
  }
`

const CSSvariables = styled.div`
margin-top: -140px;
position: relative;
background-color: rgba(37, 77, 50, 0.8);
height: 120vh;
--items-per-screen: 3;

@media (max-width: 800px) {
  --items-per-screen: 2;
}

@media (max-width: 500px) {
  --items-per-screen: 1;
}
`

const Header = styled.div`
  display: flex;
  justify-content: end;
  padding-top: 200px;

  @media (max-width: 500px) {
    justify-content: center;
  }
`

const ProgressBar = styled.div`
  display: flex;
  align-items: end;
  padding-bottom: 3rem;
  padding-right: 8rem;

  @media (max-width: 500px) {
    justify-content: center;
    align-items: center;
    padding-right: 0;
  }
`

const ProgressItem = styled.div`
  margin: 0 .2rem;
  width: 1rem;
  height: 2px;
  border-radius: 5px;
  background-color: ${props => props.active === props.i ? "#81E93C" : "#254D32"};
`

const Container = styled.div`
  overflow: hidden;
  padding: 0;
  margin: 0;
  width: 100vw;
  display: flex;
  justify-content: center;

  &:hover ${Button} {
    color: #81E93C; 
  }
 `
 
 const Slider = styled.div`
  width: 90%;
  display: flex;
  // justify-content: center;
  align-items: center;
  transform: translateX(-${props => props.active}00%);
  transition: transform 0.3s ease-in-out;
`

const ClientsContainer = styled.div`
flex: 0 0 calc(100% / var(--items-per-screen));
max-width: calc(100% / var(--items-per-screen));
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
`

const Img = styled.img`
  height: 200px;
  width: auto;
  object-fit: cover;
  border-radius: 50%;
  border: solid 1px #254D32;
  transition all 0.3s ease-in-out;
  margin-bottom: 10px;
`

const Name = styled.h3`
  color: #81E93C;
  position: relative;
  left: 20%;
  padding-bottom: 10px;
  margin: 0;
  font-family: 'Bebas Neue', cursive;
  align-self: start;
  width: 60%;
`

const Desc = styled.p`
  color: white;
  align-self: center;
  margin: 0;
  font-size: 15px;
  align-text: start;
  width: 60%;
`


function Clients() {
  
  const sliderRef = useRef();
  
  
  const [active, setActive] = useState(0)
  
  const [itemsPerScreen, setItemsPerScreen] = useState(() => {
    if (800 < window.innerWidth) {
      return 3;}
    if (window.innerWidth < 500) {
      return 1;
    }    
    if ( 500 < window.innerWidth < 800) {
      return 2;
    }    
  });
  
  const [barNumber, setBarNumber] = useState(0);

useEffect(()=> {
  window.addEventListener("resize", ()=> {
    if (window.innerWidth < 500) {
          setItemsPerScreen(1);
      }    
    if ( 500 < window.innerWidth ) {
      setItemsPerScreen(2);
      }
    if (800 < window.innerWidth) {
          setItemsPerScreen(3);
      }
  })
}, []);

  


  useEffect(() => {
   const calculateProgressBar = (sliderRef) => {
    const itemsNumber = sliderRef.current.children.length;
    setBarNumber(Math.ceil(itemsNumber / itemsPerScreen));
  }
  calculateProgressBar(sliderRef)
}, [itemsPerScreen])

  const array = [...Array(barNumber)];

  return (
  <CSSvariables itemsPerScreen={itemsPerScreen}>
  <Header>
    <ProgressBar>
      {array.map((elem, i) => (<ProgressItem key={i} i={i} active={active}/>))}
    </ProgressBar>
  </Header>
    <Container>
      <Button onClick={() => { setActive(prevState => prevState <= 0 ? barNumber - 1 : prevState - 1)}} direction="left">&#8249;</Button>
      <Slider ref={sliderRef} active={active}>
        <ClientsContainer>
            <Img src={Silhouette} />
            <Name>Test Name</Name>
            <Desc>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</Desc>
        </ClientsContainer>
        <ClientsContainer>
            <Img src={Silhouette} />
            <Name>Test Name</Name>
            <Desc>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</Desc>
        </ClientsContainer>
        <ClientsContainer>
            <Img src={Silhouette} />
            <Name>Test Name</Name>
            <Desc>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</Desc>
        </ClientsContainer>
        <ClientsContainer>
            <Img src={Silhouette} />
            <Name>Test Name</Name>
            <Desc>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</Desc>
        </ClientsContainer>
        <ClientsContainer>
            <Img src={Silhouette} />
            <Name>Test Name</Name>
            <Desc>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</Desc>
        </ClientsContainer>
        <ClientsContainer>
            <Img src={Silhouette} />
            <Name>Test Name</Name>
            <Desc>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</Desc>
        </ClientsContainer>
        <ClientsContainer>
            <Img src={Silhouette} />
            <Name>Test Name</Name>
            <Desc>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</Desc>
        </ClientsContainer>
        <ClientsContainer>
            <Img src={Silhouette} />
            <Name>Test Name</Name>
            <Desc>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</Desc>
        </ClientsContainer>
        <ClientsContainer>
            <Img src={Silhouette} />
            <Name>Test Name</Name>
            <Desc>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</Desc>
        </ClientsContainer>
      </Slider>
      <Button onClick={() => {setActive(prevState => prevState >= barNumber - 1 ? 0 : prevState + 1)}} direction="right">&#8250;</Button>
    </Container>
    </CSSvariables>
  );
}

export default Clients;