import React from 'react'
import styled from 'styled-components';
import crossfit from "../Imagenes/crossfit.jpg"
import pilates from "../Imagenes/pilates.jpg"
import aerobics from "../Imagenes/aerobics.jpg"
import weights from "../Imagenes/weights.jpg"

const Container = styled.div`
    margin-bottom: -140px;
    padding-bottom: 200px;
    padding-top: 30px;
    position: relative;
    z-index: 0;
    width: 100%;
    height: 100%;

    @media (max-width: 900px) {
        margin-top: 80px;
    }
`

const SubContainer = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    jusitfy-content: center;
    align-items: center;
    
    @media (max-width: 800px) {
       flex-direction: ${props => props.inverted ? "column-reverse" : "column" };
    }
`

const Img = styled.img`
    width: 50%;
    height: 100%;
    box-size: border-box;
    object-fit: cover;
    margin: 0 30px;
    border: 2px solid #81E93C;
    border-radius: 10px;
    padding: 3px;
    display: flex;

    @media (max-width: 800px) {
        width: 70%;
        margin: 0 0 20px 0;
    }

    @media (max-width: 500px) {
        width: 80%;
    }

    @media (max-width: 400px) {
        width: 90%;
      }
`

const InfoContainer = styled.div`
    width: 50%;
    height: auto;
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    padding: 40px;

    @media (max-width: 800px) {
        padding: 10px 10px 10px 20px;
        width: 70%;
    }

    @media (max-width: 500px) {
        width: 90%;
    }
`

const Title = styled.h2`
    margin: 0;
    padding: 0;
    color: white;
    font-size: 80px;
    font-family: 'Bebas Neue', cursive;
    letter-spacing: 0;
    font-weight: 1000;

    @media (max-width: 800px) {
        margin: 0;
        font-size: 65px;
    }

    @media (max-width: 700px) {
        font-size: 55px;
    }

    @media (max-width: 350px) {
        font-size: 45px;
    }
`

const SubTitle = styled.h2`
    margin: 0;
    padding: 0;
    color: #81E93C;
    font-size: 30px;
    font-family: 'Bebas Neue', cursive;
    letter-spacing: 0;
`

const Desc = styled.p`
color: white;

@media (max-width: 600px) {
    font-size: 14px;
}
`

const data = [
    {
        title: "Crossfit",
        desc: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Adipisci natus voluptatem ipsam rem, hic, consectetur at, ex repudiandae voluptates impedit ad itaque. Voluptates, facere iure. Rem vitae accusamus voluptatibus repellendus eius, mollitia accusantium molestias hic autem, quidem fugiat, obcaecati aut.",
        img: crossfit,
        inverted: true
    },
    {
        title: "Pilates",
        desc: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Adipisci natus voluptatem ipsam rem, hic, consectetur at, ex repudiandae voluptates impedit ad itaque. Voluptates, facere iure. Rem vitae accusamus voluptatibus repellendus eius, mollitia accusantium molestias hic autem, quidem fugiat, obcaecati aut.",
        img: pilates,
        inverted: false
    },
    {
        title: "Aerobics",
        desc: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Adipisci natus voluptatem ipsam rem, hic, consectetur at, ex repudiandae voluptates impedit ad itaque. Voluptates, facere iure. Rem vitae accusamus voluptatibus repellendus eius, mollitia accusantium molestias hic autem, quidem fugiat, obcaecati aut.",
        img: aerobics,
        inverted: true
    },
    {
        title: "Weights",
        desc: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Adipisci natus voluptatem ipsam rem, hic, consectetur at, ex repudiandae voluptates impedit ad itaque. Voluptates, facere iure. Rem vitae accusamus voluptatibus repellendus eius, mollitia accusantium molestias hic autem, quidem fugiat, obcaecati aut.",
        img: weights,
        inverted: false
    }
]

const Clases = () => {

        return(       
            <Container>
                {data.map(obj =>
                    obj.inverted 
                    ?
                    (<SubContainer inverted={obj.inverted}>
                            <Img src={obj.img}/>
                            <InfoContainer>
                                <Title>{obj.title}</Title>
                                <SubTitle>Lorem ipsum dolor sit</SubTitle>
                                <Desc>{obj.desc}</Desc>
                            </InfoContainer>
                    </SubContainer>) 
                    :
                    (<SubContainer inverted={obj.inverted}>
                            <InfoContainer>
                                <Title>{obj.title}</Title>
                                <SubTitle>Lorem ipsum dolor sit</SubTitle>
                                <Desc>{obj.desc}</Desc>
                            </InfoContainer>
                            <Img src={obj.img}/>
                    </SubContainer>))} 
            
            </Container>
            )
};


export default Clases;