import React from 'react'
import styled from 'styled-components';
import crossfit from "../Imagenes/crossfit.jpg"
import { Link } from 'react-router-dom';

const Container = styled.div`
    margin-bottom: -140px;
    padding-bottom: 200px;
    padding-top: 30px;
    position: relative;
    z-index: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(37, 77, 50, 0.8);

    @media (max-width: 900px) {
        margin-top: 80px;
    }
`

const SubContainer = styled.div`
    height: 100%;
    display: flex;
    jusitfy-content: center;
    align-items: center;
    
    @media (max-width: 900px) {
       flex-direction: ${props => props.inverted ? "column-reverse" : "column" };
    }
`

const Img = styled.img`
    width: 50%;
    height: auto;
    box-size: border-box;
    object-fit: cover;
    margin: 0 40px;
    border: 2px solid #81E93C;
    border-radius: 10px;
    padding: 3px;
    display: flex;

    @media (max-width: 1000px) {
        margin: 0px 20px 0px 40px;
    }

    @media (max-width: 900px) {
        width: 70%;
        margin: 0 0 20px 0;
    }

    @media (max-width: 500px) {
        width: 80%;
    }

    @media (max-width: 400px) {
        width: 90%;
      }
`

const InfoContainer = styled.div`
    width: 50%; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    position: relative;
    height: 100%;
    margin: 0;
    padding: 0 40px 0 0;

    @media (max-width: 900px) {
        padding: 0;
        width: 70%;
        margin-bottom: 40px;
    }

    @media (max-width: 500px) {
        width: 80%;
    }
`

const Title = styled.h2`
    margin: 0;
    padding: 0;
    color: white;
    font-size: 80px;
    font-family: 'Bebas Neue', cursive;
    letter-spacing: 0;
    font-weight: 1000;

    @media (max-width: 800px) {
        margin: 0;
        font-size: 65px;
    }

    @media (max-width: 700px) {
        font-size: 55px;
    }

    @media (max-width: 350px) {
        font-size: 45px;
    }
`

const SubTitle = styled.h2`
    margin: 0;
    padding: 0;
    color: #81E93C;
    font-size: 30px;
    font-family: 'Bebas Neue', cursive;
    letter-spacing: 0;
`

const Desc = styled.p`
    color: white;

@media (max-width: 600px) {
    font-size: 14px;
}
`

const Button = styled.button`
    margin-top: 10px;
    padding: 10px 20px;
    border-radius: 5px;
    border: solid 1px black;
    background-color: #81E93C;
    color: black;
    cursor: pointer;
    transition: all 0.5s ease;
    font-weight: bold;
    align-self: center;
`

const data = [
    {
        title: "Crossfit",
        desc: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Adipisci natus voluptatem ipsam rem, hic, consectetur at, ex repudiandae voluptates impedit ad itaque. Voluptates, facere iure. Rem vitae accusamus voluptatibus repellendus eius, mollitia accusantium molestias hic autem, quidem fugiat, obcaecati aut.",
        img: crossfit,
        inverted: true
    }]

const Clases = () => {

        return(       
            <Container id="Classes">
                {data.map(obj =>
                    obj.inverted 
                    ?
                    (<SubContainer inverted={obj.inverted}>
                            <Img src={obj.img}/>
                            <InfoContainer>
                                <Title>{obj.title}</Title>
                                <SubTitle>Lorem ipsum dolor sit</SubTitle>
                                <Desc>{obj.desc}</Desc>
                                <Link to="/clases">
                                <Button>Learn More</Button>
                                </Link>
                            </InfoContainer>
                    </SubContainer>) 
                    :
                    (<SubContainer inverted={obj.inverted}>
                            <InfoContainer>
                                <Title>{obj.title}</Title>
                                <SubTitle>Lorem ipsum dolor sit</SubTitle>
                                <Desc>{obj.desc}</Desc>
                                
                            </InfoContainer>
                            <Img src={obj.img}/>
                    </SubContainer>))} 
            
            </Container>
            )
};


export default Clases;