import React, {useContext} from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { DataContext } from '../Context/context';

const Container = styled.div`
  position: relative;
  box-sizing: border-box;
`

const RatesContainer = styled.div`
  display: flex;
  justify-content: space-around;
  padding: 20px 0;
  z-index: 2;
  position: relative;

  @media (max-width: 1000px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
`

const Title = styled.h2`
padding-left: 20px;
margin: 0;
  transition: all 0.5s ease;
  font-family: 'Bebas Neue', cursive;
  font-size: 50px;
`

const Subtitle = styled.p`
padding-left: 20px;
margin: 0;
font-family: 'Bebas Neue', cursive;
`

const Button = styled.button`
    padding: 10px 20px;
    border-radius: 5px;
    border: solid 1px #81E93C;
    background-color: transparent;
    color: white;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
    z-index: 5;
`

const Rates = styled.div`
  padding: 20px;
  flex: 1;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 1px solid white;
  border-radius: 25px;
  margin: 30px 30px;
  background-color: rgb(0,0,0,0.8);
  transition: all 0.5s ease-in-out;

  &:hover {
    border-color: #81E93C;
  }

  &:hover ${Title} {
    color: #81E93C;
  }

  &:hover ${Button} {
    border-color: black;
    background-color: #81E93C;
    color: black;
  }

  @media (max-width: 1000px) {
    width: 50%;
    align-items: center;
    margin: 20px 20px;
}

@media (max-width: 600px) {
  width: 70%;
  align-items: center;
  margin: 20px 20px;
}

@media (max-width: 400px) {
  width: 90%;
}
`

const Price = styled.h3`
padding-left: 20px;
margin: 5px 0;
`

const Includes = styled.p``

const Ol = styled.ol`
  margin: 0;
  padding-left: 20px;
`

const Li = styled.li`
  margin: 0;
  font-size: 15px;
  padding: 5px;
`

const Br = styled.hr`
height: 1px;
  background-color: rgb(200,200,200,0.1);
  border: none;
  margin: 0 40px 0 10px;
`


const Prices = () => {

  const { setPricePackage } = useContext(DataContext);

  return (
    <Container id="Rates">
        <RatesContainer>
          <Rates onClick={() => { 
            setPricePackage("1");
                }}>
                <Title>FULL PACKAGE</Title>
                <Subtitle>3 months program</Subtitle>
                <Price>$90,5</Price>
                <Includes>
                    <Ol>
                        <Li>
                        Lorem ipsum dolor sit
                        </Li>
                        <Br />
                        <Li>
                        Praesent convallis nibh
                        </Li>
                        <Br />
                        <Li>
                        Phasellus auctor augue
                        </Li>
                        <Br />
                    </Ol>
                </Includes>
                <Link to="/subscription" style={{textDecoration: "none", alignSelf: "center" }}>
                  <Button style={{ width: "fit-content"}}><b>Get Now</b></Button>
            </Link>
              </Rates>
              <Rates onClick={() => { 
                setPricePackage("2");
                }}>
                <Title>FULL PACKAGE</Title>
                <Subtitle>3 months program</Subtitle>
                <Price>$90,5</Price>
                <Includes>
                    <Ol>
                        <Li>
                        Lorem ipsum dolor sit
                        </Li>
                        <Br />
                        <Li>
                        Praesent convallis nibh
                        </Li>
                        <Br />
                        <Li>
                        Phasellus auctor augue
                        </Li>
                        <Br />
                    </Ol>
                </Includes>
            <Link to="/subscription" style={{textDecoration: "none", alignSelf: "center" }}>
              <Button style={{ width: "fit-content"}}><b>Get Now</b></Button>
            </Link>
              </Rates>
              <Rates onClick={() => { 
                setPricePackage("3");
                }}>
                <Title>FULL PACKAGE</Title>
                <Subtitle>3 months program</Subtitle>
                <Price>$90,5</Price>
                <Includes>
                    <Ol>
                        <Li>
                        Lorem ipsum dolor sit
                        </Li>
                        <Br />
                        <Li>
                        Praesent convallis nibh
                        </Li>
                        <Br />
                        <Li>
                        Phasellus auctor augue
                        </Li>
                        <Br />
                    </Ol>
                </Includes>
            <Link to="/subscription" style={{textDecoration: "none", alignSelf: "center" }}>
            <Button style={{ width: "fit-content"}}><b>Get Now</b></Button>
            </Link>
              </Rates>
        </RatesContainer> 
    </Container>
  )
}

export default Prices;